.fh5co-footer-style-1 .fh5co-footer-inner .links {
    padding: 0;
    margin: 0 0 20px 0;
    font-size: 18px;
}
.fh5co-footer-style-1 .fh5co-footer-inner .links li {
    display: inline-block;
    margin-right: 18px;
    list-style: none;
    font-family: "Open Sans", Arial, sans-serif;
}
.fh5co-footer-style-1 .fh5co-footer-inner {
    padding: 2em 0 1em;
    font-family: "Open Sans", Arial, sans-serif;
}

.fh5co-footer-style-1 .fh5co-footer-inner .fh5co-logo a {
    padding: 0;
    background: none;
    font-size: 40px;
    color: #4d4d4d;
    font-weight: 700;
    display: -moz-inline-stack;
    display: inline-block;
    zoom: 1;
    border-radius: 4px;
    font-family: "Montserrat", Arial, sans-serif;
}
.fh5co-footer-style-1 .fh5co-footer-inner .copyright {
    margin-bottom: 20px !important;
    margin-left: 20px;
    line-height: 1.5;
    display: inline-block;
}

.fh5co-footer-style-1 .fh5co-footer-inner .fh5co-logo {
     margin-bottom: 0; 
}
.fh5co-content-style-10 h2 {
     margin-top: 0px; 
    color: #1784fb;
}

.partner-logo {
	margin-right: 60px;
}

.fh5co-content-style-10, .fh5co-features-style-3 {
    background: #F7F7F7;
}

.fh5co-content-style-5 {
    background: #fff;
}